/* src/components/CountryCard.css */

.country-card {
  background-color: #fff;
  border-radius: 8px; /* Slightly smaller radius */
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.09); /* Adjusted shadow */
  padding: 0; /* Remove padding, apply to faces */
  margin: 6px; /* Adjusted margin */
  width: 100px; /* Significantly thinner */
  min-height: 150px; /* Adjust height as needed */
  text-align: center;
  display: flex; /* Keep flex */
  flex-direction: column;
  justify-content: flex-start;
  position: relative;
  cursor: default;
  transition: box-shadow 0.2s ease, transform 0.2s ease;
  perspective: 1000px;
  overflow: hidden; /* Hide overflowing parts of flag/content */
}

/* Add cursor pointer for clickable/flippable cards */
.country-card.clickable,
.country-card.flippable {
  cursor: pointer;
}

.country-card.clickable:hover {
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.14);
  transform: scale(1.03);
}

.country-flag {
  width: 100%; /* Full width */
  height: 50px; /* Approx 1/3rd of 150px height */
  object-fit: cover; /* Cover the area, might crop */
  display: block;
  margin: 0 0 10px 0; /* Remove horizontal/top margin, add bottom margin */
  border: none; /* Remove border */
  border-bottom: 1px solid #eee; /* Optional separator */
  border-radius: 8px 8px 0 0; /* Round top corners only */
}

.country-info {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center; /* Center vertically in remaining space */
  padding: 0 10px 10px 10px; /* Add padding below flag */
  text-align: center; /* Center text */
}

.country-name {
  font-size: 0.85rem; /* Adjust size for thinner card */
  font-weight: 600;
  margin: 0;
  color: #333;
  line-height: 1.2;
  word-wrap: break-word; /* Allow long names to wrap */
}

.country-detail {
  font-size: 0.75rem; /* Adjust size */
  color: #555;
  margin-top: 5px;
}

/* Highlight Styles */
.country-card.highlighted-incorrect {
  border: 2px solid #dc3545; /* Red border for incorrect */
}

.country-card.highlighted-correct {
  border: 2px solid #28a745; /* Green border for correct */
}

.country-card.selected-for-placing {
  outline: 3px solid #007bff; /* Blue outline when selected */
  outline-offset: 2px;
}

.country-card.disabled-card {
  opacity: 0.6;
  cursor: not-allowed;
}

/* --- Flip Animation CSS --- */
.card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

.country-card.flipped .card-inner {
  transform: rotateY(180deg);
}

.card-face {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  background-color: #fff;
  border-radius: 8px; /* Match card radius */
  display: flex;
  flex-direction: column; /* Stack flag and info */
  align-items: center; /* Center items horizontally */
  justify-content: flex-start; /* Align to top */
  padding: 0; /* Remove padding here, handled by flag/info */
  box-sizing: border-box;
}

.card-front {
  /* Face itself doesn't need specific styles now */
}

.card-back {
  transform: rotateY(180deg);
  color: #333;
  justify-content: center; /* Center back content better */
}

/* Ensure flag/info structure is maintained on back */
.card-back .country-flag {
    /* Keep flag style */
}
.card-back .country-info {
    /* Keep info style */
    padding-top: 10px; /* Add padding if flag isn't shown on back */
}
.card-back .country-name {
   /* Keep name style */
}

/* --- End Flip Animation CSS --- */

/* Add styles for highlighted cards */
.card-face.highlighted-incorrect {
  background-color: #f8d7da; /* Light red background */
  border: 2px solid #f5c6cb; /* Light red border */
}

.card-face.highlighted-incorrect .country-name,
.card-face.highlighted-incorrect .country-detail {
  color: #721c24; /* Dark red text */
}

/* Correct Highlight Styles */
.card-face.highlighted-correct {
  background-color: #d4edda; /* Light green background */
  border: 2px solid #c3e6cb; /* Light green border */
}

.card-face.highlighted-correct .country-name,
.card-face.highlighted-correct .country-detail {
  color: #155724; /* Dark green text */
}

/* Style for the card selected for placement in Cooperation Mode */
.country-card.selected-for-placing .card-front,
.country-card.selected-for-placing .card-back {
  background-color: #d4edda !important; /* Light green background */
  border: 1px solid #c3e6cb !important; /* Subtle border */
  box-shadow: 0 0 10px rgba(40, 167, 69, 0.3) !important; /* Subtle glow */
  /* Keep other face styles like radius if needed */
}

/* Responsive Design */
@media (max-width: 768px) {
  .country-card {
    width: 85px;
    min-height: 130px;
    margin: 4px;
  }
  .country-flag {
    height: 45px;
    margin-bottom: 8px;
  }
  .country-info {
     padding: 0 8px 8px 8px;
  }
  .country-name {
    font-size: 0.8rem;
  }
  .country-detail {
    font-size: 0.7rem;
  }
}

@media (max-width: 480px) {
  .country-card {
    width: 75px;
    min-height: 120px;
    margin: 3px;
  }
  .country-flag {
    height: 40px;
    margin-bottom: 6px;
  }
  .country-info {
     padding: 0 6px 6px 6px;
  }
  .country-name {
    font-size: 0.7rem;
  }
  .country-detail {
    font-size: 0.65rem;
  }
}